<template>
  <div class='common-left-component' v-if='subjectList'>
    <div class='subject'>
      <h3 class='add-link' @click='lookMore'>{{subjectCNname}}</h3>
      <ul>
        <li
          v-for='(item, index) in subjectList'
          @click='_selectItem(item, index, subjectList)'
          class='add-link'
        >{{item.title}}</li>
        <li class='add-link' @click='lookMore'>&nbsp;&nbsp;<i class="fa fa-chevron-right fa-1x"></i><i class="fa fa-chevron-right fa-1x"></i></li>
      </ul>
    </div>
  </div>
</template>

<script>
  import {getService, getServiceCount} from 'src/api/getservice.js'
  import {getCase, getCaseCount} from 'src/api/getcase.js'

  import {mapActions} from 'vuex'
  import {mapGetters} from 'vuex'

  import getRouterPath from 'src/common/js/getpath.js'

  //  1000
  const CURPAGE = 1
  const PAGECAPACITY = 8 //  configPAGECAPACITY=6


  export default {
    data () {
      return {
        subjectList: []
      };
    },
    props: {
      subject: {
        type: String,
        default: ''
      }
    },
    created () {
      // ajax case service
      if (this.subject === 'service') {
        this._getService(CURPAGE, PAGECAPACITY);
      } else if (this.subject === 'case') {
        this._getCase(CURPAGE, PAGECAPACITY);
      }
      
    },
    mounted () {
      // dom 1
      this.toggleShowFlag({flag: true});
    },
    updated () {

    },
    destroyed () {
      // this.$root.eventBus.$off('changeSubject');
    },
    watch: {
      $route(){
      }
    },
    methods: {
      ...mapActions([
        'selectItem',
        'toggleShowFlag'
      ]),
      _getService (curPage, pageCapacity) {
        var _this = this;
        getService(curPage, pageCapacity).then(function (response) {
          _this.subjectList = response.data;
          // console.log(_this.subjectList); //
        }).catch(function (error) {
          console.log(error);
        });
      },
      _getCase (curPage, pageCapacity) {
        var _this = this;
        getCase(curPage, pageCapacity).then(function (response) {
          _this.subjectList = response.data;
          // console.log(_this.subjectList); //
        }).catch(function (error) {
          console.log(error);
        });
      },
      _selectItem (item, index, contArr) {
        var _this = this; 
        // 
        var beforePath = getRouterPath(this.$route.path);

        // 
        this.$router.push({
          path: `/${_this.subject}/${item._id}`
        });
        
        // vuex   index ajax 
        this.selectItem({item, index, contArr});

        //  true false 2
        this.toggleShowFlag({flag: false});

        //   news /news
        //   /service/5a6d7cbb84d166a57b200475
        //   
        if (beforePath!==this.subject) {
          // console.log('');
          // vuex showFlag
          // true true common-content
          // this.$root.eventBus.$emit('changeSubject', false); // 

          setTimeout(() => {
            this.toggleShowFlag({flag: false});
          },10);
        }
      },
      // 
      lookMore () {
        var _this = this;
        this.$router.push({
          path: `/${_this.subject}`
        });
      }
    },
    computed: {
      ...mapGetters([
        'curPageArr'
      ]),
      subjectCNname () {
        if (this.subject === 'case') {
          return '';
        } else if (this.subject === 'service') {
          return '';
        }
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/variable.styl'

  .common-left-component
    background: $color-background-light
    .subject
      padding-left: 4%
      padding-right: 4%
      padding-top: 15px
      padding-bottom: 15px
      h3
        line-height: 36px
        height: 36px
        border-bottom: 1px dashed $color-text-basic
        font-size: $font-size-medium
        color: $color-text-blue
        font-weight: bold
      ul
        li
          white-space: nowrap
          overflow: hidden
          text-overflow: ellipsis
          line-height: 30px
          height: 30px
          font-size: $font-size-small
          color: $color-text-black
          i
            margin-left: -3px
            font-weight: normal

</style>


