<template>
  <div class='outer-subject-wrapper' >
    <div class='inner-subject-wrapper clearfix'>
      <common-left></common-left>
      <common-content 
        v-if='pageCount' 
        :queryTitle='queryTitle' 
        :queryTitleCn='queryTitleCn' 
        :originCurPage='originCurPage'
        :pageCount='pageCount'  
        :subjectCount='newsCount' 
        :subjectContent='news' 
        @curPageChange='curPageChange'
        
      ></common-content><!-- requestTitle 'news' -->
      
      <router-view/><!-- detail -->
    </div>
  </div>
</template>

<script>
  // 
  import CommonLeft from 'src/base/common-left/common-left.vue'
  import CommonContent from 'src/base/common-content/common-content.vue'
  import {PAGECAPACITY} from 'src/api/config.js'

  // api
  import {getNews, getNewsCount} from 'src/api/getnews.js'

  export default {
    data () {
      return {
        queryTitle: 'news',
        queryTitleCn: '',
        news:[],
        newsCount: 0,
        pageCount: null, // ajax 
        originCurPage: 1,
      };
    },
    components: {
      CommonLeft,
      CommonContent
    },
    created () {
      // 
      //  
      this._getNewsCount();
      this._getNews(this.curPage);
      // 
    },
    methods: {
      _getNews (curPage) {
        var _this = this;
        getNews(curPage).then(function (response) {
          //  
          _this.news = response.data;
        }).catch(function (error) {
          console.log(error);
        });
      },
      _getNewsCount () {
        var _this = this;
        getNewsCount().then(function (response) {
          //  
          _this.newsCount = response.data;
          // 
          _this.pageCount = Math.ceil(_this.newsCount / PAGECAPACITY);
        }).catch(function (error) {
          console.log(error);
        });
      },
      curPageChange (curPage) {
        // console.log(':', curPage);
        // curPage
        // ajax props
        this._getNews(curPage);
      },

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/variable.styl'

  .outer-subject-wrapper
    width: 100%
    overflow: hidden
    .inner-subject-wrapper
      width: 170%



</style>


