<template>
  <div class='header clearfix' ref='mHeader'>
    <div>
      <img class='logoare' src='./logo.png'/>
    </div>
    <h1 class='corpTitle'></h1>

    <div class='nav-bar'>
      <ul class='nav-content clearfix'>
        <li class='clearfix'>
          <router-link tag='a' to='/index' @click.native='routeAnimateIndex'>
            <i class="fa fa-home fa-1x"></i>&nbsp;&nbsp;
          </router-link>
        </li>
        <li class='clearfix'>
          <router-link tag='a' to='/aboutus' @click.native='routeAnimate'>
            <i class="fa fa-globe fa-1x"></i>&nbsp;&nbsp;
          </router-link>
        </li>
        <li class='clearfix'>
          <router-link tag='a' to='/service' @click.native='routeAnimate'>
            <i class="fa fa-list fa-1x"></i>&nbsp;&nbsp;
          </router-link>
        </li>
        <li class='clearfix'>
          <router-link tag='a' to='/news' @click.native='routeAnimate'>
            <i class="fa fa-newspaper-o fa-1x" ></i>&nbsp;&nbsp;
          </router-link>
        </li>
        <li class='clearfix'>
          <router-link tag='a' to='/case' @click.native='routeAnimate'>
            <i class="fa fa-book fa-1x"></i>&nbsp;&nbsp;
          </router-link>
        </li>
        <li class='clearfix'>
          <router-link tag='a' to='/sample' @click.native='routeAnimate'>
            <i class="fa fa-university fa-1x"></i>&nbsp;&nbsp;
          </router-link></li>
        <li class='clearfix'>
          <router-link tag='a' to='/jobs' @click.native='routeAnimate'>
            <i class="fa fa-handshake-o fa-1x"></i>&nbsp;&nbsp;
          </router-link>
        </li>
      </ul>
    </div>
    <div class='contact-us'>
      <a target="_blank" href="http://wpa.qq.com/msgrd?v=3&uin=285457718&site=qq&menu=yes">
        <i class="fa fa-qq fa-1x"></i>
        <span></span>
      </a>
    </div>

    <i class="fa fa-2x icon-bars" :class="{'fa-bars': !navShow, 'fa-close': navShow }" @click.stop='_showNav'></i>
  </div>
</template>

<script>
  import {mapActions} from 'vuex'
  import {mapGetters} from 'vuex'
  
  import {SCREENBOUNDARY} from 'src/api/config.js'

  export default {
    data () {
      return {
      };
    },
    computed: {
      ...mapGetters([
        'navShow'
      ])
    },
    methods: {
      ...mapActions([
        'showNav'
      ]),
      _showNav () {
        var _this = this;
        this.showNav({flag: !_this.navShow});
      },
      routeAnimate () {
        var scrollHeight = $('.poster-outer-wrapper').innerHeight();
        // console.log(scrollHeight);
        var curScrollTop = $("html, body").scrollTop();
        $("html, body").animate({ scrollTop: scrollHeight - 2}, 200);
        return false;
      },
      routeAnimateIndex () {
        $("html, body").animate({ scrollTop: 0}, 200);
      }
    },
    mounted () {
      //  window.onresize =   
      /*
      window.onresize = () => {
      };
      */
      window.addEventListener('resize', () => {
        var curTotalWidth = document.documentElement.clientWidth || document.body.clientWidth;
        // console.log('header resize', curTotalWidth);
        //  768 navShowtrue false
        if (curTotalWidth > SCREENBOUNDARY && this.navShow === true) {
          this.showNav({flag: false})
        }
      });
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import '~common/stylus/variable.styl'

  .header
    background: $color-background-header
    height: 102px
    position: fixed
    left: 0
    top: 0
    width: 100%
    z-index: 500

    .logoare
      height: 70px
      float:left
      margin-left:2%
      margin-top:15px

    /* corpTitleh1 SEO  */  
    .corpTitle
      font-size: 20px
      position: absolute
      left: 95px
      top: 56px
      font-size: 16px
      color: $color-text-blue
      margin-left: -100%
    
    .nav-bar
      width: 60%
      float: left
      margin-top: 60px
      margin-left: 2%
      @media (max-width: 920px)
        &
          display: none
      li
        float: left
        a
          padding-right: 10px
          font-size: $font-size-medium
          font-weight: bold
          color: $color-text-basic
          /* class  */
          &.router-link-active
            color: $color-text-blue

        a:hover
          display: inline-block
          color: $color-text-blue
          transform: scale(1.1)

    .contact-us
      position: absolute
      right: 10px
      top: 45px
      a
        color: $color-text-white
        font-size: $font-size-medium
        display: inline-block
        background-color:$color-background-green
        padding: 8px 12px
        border-radius: 20px
        span
          padding-left: 3px
    .contact-us    
      @media (max-width: 920px)
        display: none  

    .icon-bars

      display: none
      color: $color-text-blue
      float:right
      margin-right: 2%
      margin-top: 30px
      padding: 5px 15px
      border: 1px solid $color-text-blue
      border-radius: 5px
      font-size: 20px
      @media (max-width: 920px)
        &
          display: block
    .icon-bars:hover
      border: 1px solid $color-text-white
      transform: scale(1.1)

</style>


